.App {
  height: 100%;
}

#tdw-text{
	font-family: 'Open Sans', sans-serif;
	font-size: 48px;
	font-weight: 800;
  height: 100%;
  color: white;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;

	background-image: -webkit-linear-gradient(92deg,#42a1f4,#2ae0bf);
    -webkit-animation: hue 5s infinite linear;
}

@-webkit-keyframes hue {
    from {
      -webkit-filter: hue-rotate(0deg);
    }
    
    to {
      -webkit-filter: hue-rotate(360deg);
    }
}

@keyframes hue {
    from {
      -webkit-filter: hue-rotate(0deg);
    }
    
    to {
      -webkit-filter: hue-rotate(360deg);
    }
}


